.locations {
  padding-top: 3rem;
}

/* Wrapper that spans both the Locations grid and the city content with one background */
.locations-group {
  position: relative;
  isolation: isolate; /* ensure the background pseudo-element stays behind content */
}

/* Variant for city content section sharing the same map background */
.locations--city {
  /* keep default section padding from `.section` but ensure spacing above title
     doesn’t get removed by `.locations { padding-top: 0; }` */
  padding-top: 4rem;
}

/* City detail panel styling */
.locations--city {
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 3rem;
}
.locations--city > h2,
.locations--city > p {
  text-align: center;
}
.locations--city > h2 {
  font-size: 1.9rem;
  margin-bottom: 1rem;
}
.locations--city > p {
  max-width: 68ch;
  margin-left: auto;
  margin-right: auto;
}
.locations--city .city-details {
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: 14px;
  box-shadow: 0 10px 24px var(--color-shadow);
  padding: clamp(1rem, 3vw, 2rem);
}
.locations--city .city-details h3 {
  text-align: center;
  font-size: 1.5rem;
  margin: 0 0 0.75rem 0;
}
.locations--city .city-details p {
  margin: 0 0 1rem 0;
}
.locations--city .city-features {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 0.75rem;
  margin-top: 0.5rem;
}
.locations--city .city-feature {
  background: color-mix(in oklab, var(--color-surface) 80%, transparent);
  border: 1px solid var(--color-border);
  border-radius: 12px;
  padding: 0.9rem 1rem;
}
.locations--city .city-feature h4 {
  margin: 0 0 0.5rem 0;
  font-size: 1.1rem;
}
.locations--city .city-feature ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 0.4rem;
}
.locations--city .city-feature li {
  position: relative;
  padding-left: 1.25rem;
}
.locations--city .city-feature li::before {
  content: '✓';
  position: absolute;
  left: 0;
  top: 0;
  color: var(--color-primary);
  font-weight: 700;
}

@media (max-width: 720px) {
  .locations--city > h2 { font-size: 1.6rem; }
  .locations--city .city-features { grid-template-columns: 1fr; }
}

/* Full-bleed map background spanning the entire screen width for the whole group */
.locations-group::before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100vw; /* stretch across the entire screen width */
  height: 100%;
  background-image: image-set(
    url('/public/images/map.avif') type('image/avif'),
    url('/public/images/map.webp') type('image/webp')
  );
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  z-index: -1; /* keep it behind the section content */
}
/* Dark mode background */
[data-theme='dark'] .locations-group::before {
  background-image: image-set(
    url('/public/images/map_dark.avif') type('image/avif'),
    url('/public/images/map_dark.webp') type('image/webp')
  );
}


.locations__header {
  text-align: center;
  margin-bottom: 1rem;
  position: relative;
  z-index: 1;
}

/* Improve text readability over the map background */
.locations .section__subtitle {
  text-shadow: none;
}

/* Slight backdrop for subtitle on busy areas */
.locations .section__subtitle {
  display: inline-block;
  padding: 0.35rem 0.6rem;
  border-radius: 8px;
  background: color-mix(in oklab, var(--color-background) 30%, transparent);
}

[data-theme='dark'] .locations .section__subtitle {
  background: color-mix(in oklab, var(--color-light) 8%, transparent);
}

.locations__grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  max-width: 1000px;
  margin: 0 auto;
  justify-content: center;
}

@media (max-width: 900px) {
  .locations__grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 640px) {
  /* Tighter section padding for mobile */
  .locations.section {
    padding: 1.75rem 1rem;
  }

  .locations__header {
    margin-bottom: 0.75rem;
  }

  .locations .section__subtitle {
    font-size: 1rem;
    margin-bottom: 1rem;
  }

  /* Compact vertical list (no horizontal scrolling) */
  .locations__grid {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 0.75rem;
  }

  /* Make each card a compact row to reduce height */
  .location-card {
    flex-direction: column;
    align-items: stretch;
    border-radius: 10px;
  }

  /* Show media on mobile as a smaller banner */
  .location-card__media {
    display: block;
    height: 120px;
  }

  .location-card__body {
    padding: 0.75rem;
  }

  .location-card__title {
    font-size: 1.05rem;
    margin-bottom: 0.25rem;
  }

  .location-card__blurb {
    font-size: 0.95rem;
    margin-bottom: 0.25rem;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}

.location-card {
  display: flex;
  flex-direction: column;
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: 12px;
  overflow: hidden;
  text-decoration: none;
  color: var(--color-text);
  transition:
    transform 0.2s ease,
    box-shadow 0.2s ease,
    border-color 0.2s ease,
    background-color 0.2s ease;
  max-width: 360px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.location-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px var(--color-shadow);
  border-color: var(--color-primary);
}

/* icon removed */

.location-card__body {
  padding: 0.75rem 1rem 0.9rem;
}

.location-card__media {
  height: 160px;
  background: var(--color-border);
  overflow: hidden;
}
.location-card__media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.location-card__title {
  margin: 0 0 0.2rem 0;
  font-size: 1rem;
}

.location-card__blurb {
  margin: 0 0 0.5rem 0;
  color: var(--color-text-secondary);
  font-size: 0.95rem;
}

.location-card__cta {
  font-weight: 600;
  color: var(--color-primary);
}

.city-location-info {
  background: linear-gradient(
    135deg,
    var(--color-primary) 0%,
    color-mix(in oklab, var(--color-primary) 20%, transparent) 100%
  );
  color: var(--color-secondary);
  padding: 1.5rem;
  border-radius: var(--border-radius);
  margin-top: 1.5rem;
  text-align: center;
}

.city-location-info p {
  margin: 0;
  line-height: 1.6;
  font-size: 1.1rem;
}

.city-location-info strong {
  color: var(--color-secondary);
}
